
body {
  font-family: 'Comic Sans MS', cursive, sans-serif;
  background: linear-gradient(to right, #ffd6e9, #ffe4f7, #e0f7ff);
  color: #333;
  padding: 1rem;
}

h1 {
  text-align: center;
  color: #ff69b4;
}

button {
  font-size: 1rem;
  padding: 0.5rem 1rem;
  margin: 0.3rem;
  border-radius: 10px;
  border: none;
  cursor: pointer;
}

button:hover {
  opacity: 0.9;
}

.quiz-card {
  background: #fff;
  padding: 1rem;
  margin: 1rem auto;
  border-radius: 12px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.1);
  max-width: 600px;
}
